  width: 100%;
  height: 100%;
  overflow: hidden;
  .contrast-body {
    width: 100%;
    height: calc(100% - 210px);
    overflow: hidden;
    .ivu-col-span-12 {
      height: 100%;
      width: 49.6% !important;
      .map1,
      .map2 {
        width: 100%;
        height: 100%;
        background: #fff;
        position: relative;

        .el-select1 {
          width: 150px;
          position: absolute;
          top: 15px;
          left: 65px;
        }

        .el-select2 {
          width: 150px;
          position: absolute;
          top: 15px;
          left: 230px;
        }
      }

      .map1 {
        #contrast-map1 {
          width: 100%;
          height: 100%;
        }
      }

      .map2 {
        #contrast-map2 {
          width: 100%;
          height: 100%;
        }
      }
    }


  }

  .contrast-footer {
    height: 190px;
    margin-top: 15px;
  }
}
.signB{
  font-weight: bold;
}
.red{
  color: #ff0000;
}
.green{
  color: #008000;
}
#contrast>>>.ivu-table td{
  height: 35px !important;
}
