


















































































































































































































































































































































































































#tablet{
  position: relative;
  .queryBox {
    height: 50px;
    border-bottom: 1px solid #d3d3d3;
    margin: 0 15px 15px;
    position: relative;
    overflow: inherit;
    .queryBtns {
      float: right;
      margin-top: 14px;
    }
  }
  .content_box {
    margin: 0 15px 0;
    height: calc(100% - 65px);
    overflow: hidden;
    .page_box{
      width: 100%;
      padding: 15px;
      text-align: right
    }
  }
  .btn-add {
    background-color: #01c296;
    border-color: #01c296;
    &:hover, &:focus {
      background-color: #05ab85;
      border-color: #05ab85;
    }
  }
  .btn-download {
    background-color: #1ccf20;
    border-color: #1ccf20;
    &:hover, &:focus {
      background-color: #13bb17;
      border-color: #13bb17;
    }
  }
  .add_box, .Comparison_box, .map_box{
    height: calc(100% - 30px);
    width: calc(100% - 30px);
    position: absolute;
    right: -120%;
    top:0;
    z-index: 1356;
    background: #fff;
    transition: all 0.8s ease;
    border-radius: 0 0 5px 5px;
    .closeBtn{
      position: absolute;
      right:10px;
      bottom:10px;
      z-index: 131531313;
    }
  }
  .addActive, .MapActive, .ComparisonActive{
    right: 15px;
    top: 14px;
  }
}
